import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Card, Form, Input, Select } from 'antd';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { SelectAjax } from 'components/common/index';
import { getYearEducations } from 'services/yearEducationForEnrollmentPlan';
import { getCourses } from 'services/courseForEnrollmentPlan';
import { getAreas } from 'services/areaForEnrollmentPlan';
import {
  getEnrollSolutionsById,
  createEnrollSolutions,
  updateEnrollSolutions,
} from 'services/listEnrollment';

@Form.create({ name: 'FormEnrollmentPlan' })
class FormEnrollmentPlan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      enrollSolutionsId: false,
      initialValue: {},
    };
  }

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ enrollSolutionsId: match.params.id });
      this.getEnrollSolutionsById(match.params.id);
      // console.log(this.state.enrollSolutionsId);
    }
  };

  getEnrollSolutionsById = async id => {
    const res = await getEnrollSolutionsById(id);
    const initialValue = this.handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleInitialValue = initialValue => {
    const initialValueDefault = {
      id: '',
      label: '',
      courseId: '',
      areaids: '',
      areas: '',
      minTotalPoint: '',
      minPointPerSubject: '',
      noOfStudent: '',
      reviewTypeId: '',
      yearEduId: '',
    };
    return { ...initialValueDefault, ...initialValue };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }

      // console.log(values);
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { enrollSolutionsId } = this.state;
    try {
      if (!enrollSolutionsId) {
        console.log(values);
        await createEnrollSolutions({
          id: values.id,
          label: values.label,
          courseId: values.courseId,
          areaids: values.areaids.join(),
          minTotalPoint: values.minTotalPoint,
          minPointPerSubject: values.minPointPerSubject,
          noOfStudent: values.noOfStudent,
          reviewTypeId: values.reviewTypeId,
          yearEduId: values.yearEduId,
        });
      } else {
        await updateEnrollSolutions({
          id: values.id,
          label: values.label,
          courseId: values.courseId,
          areaids: values.areaids.join(),
          minTotalPoint: values.minTotalPoint,
          minPointPerSubject: values.minPointPerSubject,
          noOfStudent: values.noOfStudent,
          reviewTypeId: values.reviewTypeId,
          yearEduId: values.yearEduId,
        });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="">
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Mã phương án" hasFeedback>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên phương án" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khu vực" hasFeedback>
              {getFieldDecorator('areaids', {
                initialValue: initialValue.areaIds,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<SelectAjax service={getAreas} mode="multiple" />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Xét theo" hasFeedback>
              {getFieldDecorator('reviewTypeId', {
                initialValue: initialValue.reviewTypeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option id="1" key="1" value="1">
                    Học bạ
                  </Option>
                  <Option id="2" key="2" value="2">
                    Thi THPT
                  </Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khóa học" hasFeedback>
              {getFieldDecorator('courseId', {
                initialValue: initialValue.courseId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<SelectAjax service={getCourses} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Năm học" hasFeedback>
              {getFieldDecorator('yearEduId', {
                initialValue: initialValue.reviewTypeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Điểm tổng" hasFeedback>
              {getFieldDecorator('minTotalPoint', {
                initialValue: initialValue.minTotalPoint,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Điểm chi tiết" hasFeedback>
              {getFieldDecorator('minPointPerSubject', {
                initialValue: initialValue.minPointPerSubject,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số lượng tuyển sinh" hasFeedback>
              {getFieldDecorator('noOfStudent', {
                initialValue: initialValue.noOfStudent,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormEnrollmentPlan {...props} />
  </Page>
));



